* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

// Only display content to screen readers
//
// See: http://a11yproject.com/posts/how-to-hide-content/
.sr-only {
  position: absolute;
  width:    1px;
  height:   1px;
  margin:   -1px;
  padding:  0;
  overflow: hidden;
  clip:     rect(0, 0, 0, 0);
  border:   0;
}
